<template>
  <div class="about">
    <div>
      <button @click="logout">logout</button>
    </div>
    <div class="space"/>
    <div class="twitcharea">
      <!--
      <iframe class="twitchchnnel"
              src="https://player.twitch.tv/?channel=it0sat0&parent=it0sat0.github.io"
              id="ifr"
              frameborder="0" 
              allowfullscreen="true" 
              scrolling="no" 
              height="567" 
              width="930">
      </iframe>
      -->
      <iframe class="twitchchnnel"
              src="https://player.twitch.tv/?channel=it0sat0&parent=it0sat0.github.io" 
              frameborder="0" 
              allowfullscreen="true" 
              scrolling="no" 
              height="567" 
              width="930"></iframe>
      <div class="mousebutton" @click="getxy"/>
    </div>
    <div class="uichanger">
      <el-button type="primary" v-on:click="gotoBefore">Before</el-button>
      <el-button type="primary" v-on:click="gotoUI1">After1</el-button>
      <el-button type="primary" v-on:click="gotoUI2">After2</el-button>
    </div>
    <div class="controller">
      <div class="wasd">
        <button @click="colorChanger('w')" v-if="buttonChanger === 0"> ↑ </button>
        <button class="RedButton" @click="colorChanger('w')" v-if="buttonChanger === 1"> ↑ </button>
        <button class="BlueButton" @click="colorChanger('w')" v-if="buttonChanger === 2"> ↑ </button>
        <button class="YellowButton" @click="colorChanger('w')" v-if="buttonChanger === 3"> ↑ </button>
        <div class="wasd-list">
          <button @click="colorChanger('a')" v-if="buttonChanger === 0">←</button>
          <button class="RedButton" @click="colorChanger('a')" v-if="buttonChanger === 1">←</button>
          <button class="BlueButton" @click="colorChanger('a')" v-if="buttonChanger === 2">←</button>
          <button class="YellowButton" @click="colorChanger('a')" v-if="buttonChanger === 3">←</button>
          <div class="wasd-text" v-if="sendTimer === true"> movement </div>
          <div class="wasd-text" v-if="sendTimer === false"> 送信中 </div>
          <button @click="colorChanger('d')" v-if="buttonChanger === 0">→</button>
          <button class="RedButton" @click="colorChanger('d')" v-if="buttonChanger === 1">→</button>
          <button class="BlueButton" @click="colorChanger('d')" v-if="buttonChanger === 2">→</button>
          <button class="YellowButton" @click="colorChanger('d')" v-if="buttonChanger === 3">→</button>
        </div>
        <button @click="colorChanger('s')" v-if="buttonChanger === 0"> ↓ </button>
        <button class="RedButton" @click="colorChanger('s')" v-if="buttonChanger === 1"> ↓ </button>
        <button class="BlueButton" @click="colorChanger('s')" v-if="buttonChanger === 2"> ↓ </button>
        <button class="YellowButton" @click="colorChanger('s')" v-if="buttonChanger === 3"> ↓ </button>
      </div>
      <div class="wasd">
        <button @click="colorChanger('vw')" v-if="buttonChanger === 0"> ↑ </button>
        <button class="RedButton" @click="colorChanger('vw')" v-if="buttonChanger === 1"> ↑ </button>
        <button class="BlueButton" @click="colorChanger('vw')" v-if="buttonChanger === 2"> ↑ </button>
        <button class="YellowButton" @click="colorChanger('vw')" v-if="buttonChanger === 3"> ↑ </button>
        <div class="wasd-list">
          <button @click="colorChanger('va')" v-if="buttonChanger === 0">←</button>
          <button class="RedButton" @click="colorChanger('va')" v-if="buttonChanger === 1">←</button>
          <button class="BlueButton" @click="colorChanger('va')" v-if="buttonChanger === 2">←</button>
          <button class="YellowButton" @click="colorChanger('va')" v-if="buttonChanger === 3">←</button>
          <div class="wasd-text" v-if="sendTimer === true"> view </div>
          <div class="wasd-text" v-if="sendTimer === false"> 送信中 </div>
          <button @click="colorChanger('vd')" v-if="buttonChanger === 0">→</button>
          <button class="RedButton" @click="colorChanger('vd')" v-if="buttonChanger === 1">→</button>
          <button class="BlueButton" @click="colorChanger('vd')" v-if="buttonChanger === 2">→</button>
          <button class="YellowButton" @click="colorChanger('vd')" v-if="buttonChanger === 3">→</button>
        </div>
        <button @click="colorChanger('vs')" v-if="buttonChanger === 0"> ↓ </button>
        <button class="RedButton" @click="colorChanger('vs')" v-if="buttonChanger === 1"> ↓ </button>
        <button class="BlueButton" @click="colorChanger('vs')" v-if="buttonChanger === 2"> ↓ </button>
        <button class="YellowButton" @click="colorChanger('vs')" v-if="buttonChanger === 3"> ↓ </button>
      </div>
    </div>
    <div class="chest">
      <!--<button @click="colorChanger('c')">click</button>-->
      <button @click="colorChanger('cr')">craft</button>
      <button @click="colorChanger('op')">chest</button>
      <button @click="colorChanger('cl')">close</button>
    </div>
  </div>
</template>

<script>
  import firebase from 'firebase'
  import axios from "axios"
  import tmi from "tmi.js"

  export default {
    data() {
      return {
        channelName: this.$_getchannel(),
        username: this.$_getusername(),
        password: this.$_getpassword(),
        channelURL: ``,
        email: this.$_getuseremail(),
        ChatCommand: '',
        sendTimer: true,
        timeoutTimer: '',
        buttonChangeTimer: true,
        buttonChanger: 0
      }
    },
    created() {
      //外す必要あり
      //this.channelURL = `https://player.twitch.tv/?channel=${this.channelName}&parent=it0sat0.github.io`
      //console.log("channelURL:"+this.channelURL)
    },
    mounted: function(){
      // 初期化処理
      //外す必要あり
      //var obj=document.getElementById("ifr")
      //obj.removeAttribute("src")
      //document.getElementById("ifr").src = this.channelURL
      this.timeout()
    },
    methods: { 
      gotoBefore(){
        this.$router.push('/about')
      },
      gotoUI1(){
        this.$router.push('/ui1')
      },
      gotoUI2(){
        this.$router.push('/ui2')
      },
      colorChanger(command){
        clearTimeout(this.timeoutTimer)
        if(this.buttonChanger<3 && this.buttonChangeTimer){
          if(command !='cr' && command !='op' && command !='cl'){
            this.buttonChanger++
          }
          setTimeout(() => {
              this.post(command)
              console.log("post")
              this.buttonChangeTimer=false
            }
            ,1000 //時間変更可能
          )
          if(this.sendTimer){
            this.sendTimer = false
            setTimeout(() => {
                this.sendTimer = true
                this.buttonChangeTimer=true
                this.buttonChanger=0
              }
              ,3000 //時間変更可能
            )
          }
        }
        this.timeout()
      },
      func() {
       console.log(this.channelURL)
      },
      getxy: function(e){
        clearTimeout(this.timeoutTimer)
        console.log("sendTimer"+this.sendTimer)
        if(this.sendTimer){
          this.post("m,"+(e.offsetX/930)+","+(e.offsetY/567))
          console.log("e.offsetX"+ e.offsetX/930)
          console.log("e.offsetY"+ e.offsetY/567)
          this.sendTimer = false
        }
        setTimeout(() => {
          this.sendTimer = true
          }
          ,3000 //時間変更可能
        )
        this.timeout()
      },
      timeout: function(){
        this.timeoutTimer = setTimeout(() => {
          alert("一定時間操作がなかったため、ログアウトします")
          clearTimeout(this.timeoutTimer)
          this.logout()
          }
          ,600000 //時間変更可能
        )
      },
      check: function(){
        //外す必要あり
        //console.log(this.channelURL)
        //var obj=document.getElementById("ifr")
        //obj.removeAttribute("src")
        //document.getElementById("ifr").src = this.channelURL
      },
      logout: function(){
        clearTimeout(this.timeoutTimer)
        this.logouotRequest(this.email)
        firebase.auth().signOut();
        clearTimeout(this.timeoutTimer)
        this.$router.push('/')
      },
      logouotRequest: async function(username){
        const params = new URLSearchParams();
        params.append("name",username);
        await axios.post("http://localhost:8040/logout", params)
        //await axios.post("https://asia-east1-twitch-MCI.cloudfunctions.net/logout", params)
        alert("ログアウトしました")
      },
      post(command){
        console.log("OK")
        /*
        const opts = {
          channels: [this.channelName],
          identity: {
            username: this.username,
            password: this.password 
          }
        };
        */
        const opts = {
          channels: ["it0sat0"],
          identity: {
            username: "it0sat0sub",
            password: "oauth:3rl5k8t3wz5q4brrex0alg0efbr1h2"
          }
        };

        // Create a client with our options
        const client = new tmi.client(opts);

        client.connect();

        // Register our event handlers (defined below)
        client.on('connected', onConnectedHandler);

        function onConnectedHandler (addr, port) {
          console.log(`* Connected to ${addr}:${port}`);
          client.say('it0sat0', `${command}`);
        }
      },
    }
  }
</script>

<style>
